





































import { Component, Vue } from 'vue-property-decorator'

import { accountStore } from "@/stores/modules/account"
// import { IEventTypes, eventStore } from "@/stores/modules/event"
import { dialogStore } from "@/stores/modules/dialog"
// import { IMemberTypes, memberStore } from "@/stores/modules/member"

import HeaderComp from "@/components/HeaderComp.vue"
import FooterComp from "@/components/FooterComp.vue"
import ProgressComp from "@/components/ProgressComp.vue"


@Component({
  name: 'App',
  components: {
    HeaderComp,FooterComp,ProgressComp
  },
  computed: {
    snackBar(){
      return dialogStore.snackBar
    }
  }
})
export default class extends Vue {
  created(){
    accountStore.onAuthStateChanged(); //   
    console.log('App launched');
    console.log('this', this.$route);
  }

  moundted(){
    this.$validator.localize('en', {
      custom : {
        title: {
          required : (field:any) => `${field}   `,
          max: (field:any,count:number) => `${field} ${count}    `
        },
        place: {
          required : (field:any) => `${field}   `,
        },
        contents: {
          required : (field:any) => `${field}   `,
          min : (field:any, count:number) => ` ${field} ${count}  `
        },
        date : {
          required : (field:any) => `${field}  `
        },
        name: {
          required : (field:any) => `${field}   `,
          max: (field:any,count:number) => `${field} ${count}    `
        },
        mbti: {
          max: (field:any,count:number) => `${field}  `,
          min : (field:any, count:number) => `${field}  `,
        },
        // address: {
        //   required : (field:any) => `${field}   `,
        //   max: (field:any,count:number) => `${field} ${count}    `
        // }
      }
    });
  }
  
  onScroll(){
  }

  closeSnackbar(){
    dialogStore.hideSnackbar();
  }
}
